.login{
    width: 100%;
   height: 100vh;
   background-image:linear-gradient(to top,rgba(0,0,0,0.1),rgba(0,0,0,0.9)), url(./netflix\ background.png) ;
   background-size: cover;
   background-repeat: no-repeat;
   overflow: hidden;
}
.login_header img{
    position: fixed;
    top: 20px;
    left: 10px;
}

img[alt='li']{ 
   max-width: 8em;
}
.login_header button {
    position: fixed;
    top: 20px;
    right:20px;
    background-color: #fc0a0a;
    color: #ffff;
    border: 0;
    padding: 1em 2em;
    border-radius: 2em; 
}

.login_body {
    color:#ffff;
     position: absolute;
     top: 50%;
     transform: translateY(-50%);
     left: 0;
     right: 0;
    text-align: center;
}
.login_body h1 {
    font-size: 2em;
}
.login_body h3 {
    font-size: 1em;
}
.login_body h2,h1,h3 {
    font-weight: 700;
    padding-bottom: .2em;
}
/* -------------- */

.login_form input {
    width: 25%;
    padding: 0.8em 1em;
     border: none;
    font-size: 0.9em;
   border-top-left-radius: 2em;
   border-bottom-left-radius:2em ;
}

.login_form button {
    padding: 0.8em 1em;
    border: 0;
    background-color: #fc0a0a;
    color: #ffff;
    font-size: 0.9em;
    letter-spacing: 2px;
    border-top-right-radius: 2em;
    border-bottom-right-radius:2em ;
}
.login_form button:hover ,.login_header button:hover{
    color: #fc0a0a;
    background-color: #ffff;
    cursor: pointer;
}
input::placeholder {
    color: rgb(62, 65, 82);
    text-align: left;
}