.navbar {
    position: fixed;
    top: 0;
    padding: 3em;
    width: 100%;
    z-index: 1;
    box-sizing: border-box;
    
}
.navbar_content {
   display: flex;
   justify-content: space-between;
   align-items: center;
}

img[alt='li']{
    width: 20%;
}
img[alt='si']{
    width: 4%;
}